import React from 'react';
import { Bar } from 'react-chartjs-2';
import { Chart as ChartJS } from 'chart.js/auto'

const state = {
  labels: ['Documents Scanned', 'Documents Approved', 'Documents Rejected'],
  datasets: [
    {
      label: 'Scanned Results',
      backgroundColor: 'rgba(75,192,192,1)',
      borderColor: 'rgba(0,0,0,1)',
      borderWidth: 2,
      data: [65, 59, 6 ]
    }
  ]
}
  
  
  
 

const BarChart = () => {
  return (
    <Bar
          data={state}
          options={{
            title:{
              display:true,
              text:'Documnet Scanning stats',
              fontSize:20
            },
            legend:{
              display:true,
              position:'right'
            }
          }}
        />
  )
}

export default BarChart